import React, { useState, useEffect } from 'react';
import { useAuth } from "../../common/context/AuthContext";
import { useNavigate } from 'react-router-dom';
import AdminNavbar from '../components/AdminNavbar';
import { 
  UnifiedBackground, 
  UnifiedCard, 
  UnifiedButton,
  useScrollAnimation,
  SharedStyles
} from "../../common/components/SharedTheme";
import { supabase } from "../../common/services/supabaseClient"
import pinTransactionService, { PIN_ACTION_TYPES } from '../../services/pinTransactionService';
import { PinBalance, PinTransactionTable, PinStatsCards, ActionTypeBadge, PinChangeIndicator } from '../../components/PinComponents';
import AdminPinRequestsTab from '../../components/AdminPinRequestsTab';
import { Search, Plus, Edit, Trash2, Users, X, Loader, Pin, Target, CheckCircle, XCircle, AlertCircle, FileText, Clock, Award, Eye, Send } from 'lucide-react';
import { 
  SkeletonTable, 
  SkeletonPageHeader, 
  SkeletonSearchFilters 
} from '../components/skeletons';

function AdminPins() {
  const { user, logout } = useAuth();
  const navigate = useNavigate();
  useScrollAnimation();

  // State management
  const [pinRequests, setPinRequests] = useState([]);
  const [filteredRequests, setFilteredRequests] = useState([]);
  const [promoters, setPromoters] = useState([]);
  const [filteredPromoters, setFilteredPromoters] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [statusFilter, setStatusFilter] = useState('all');
  const [activeTab, setActiveTab] = useState('requests'); // 'requests' or 'direct'
  const [loading, setLoading] = useState(true);
  const [submitting, setSubmitting] = useState(false);
  
  // Request management states
  const [selectedRequest, setSelectedRequest] = useState(null);
  const [showDetailsModal, setShowDetailsModal] = useState(false);
  const [showApprovalModal, setShowApprovalModal] = useState(false);
  const [approvalData, setApprovalData] = useState({
    action: 'approve', // 'approve' or 'reject'
    adminNotes: ''
  });

  // Direct pin allocation states
  const [showPinModal, setShowPinModal] = useState(false);
  const [editingPromoter, setEditingPromoter] = useState(null);
  const [formData, setFormData] = useState({
    promoterId: '',
    pins: '',
    action: 'add' // 'add' or 'subtract'
  });
  const [formErrors, setFormErrors] = useState({});
  const [toast, setToast] = useState({ show: false, message: '', type: '' });

  // Toast utility function
  const showToast = (message, type = 'success') => {
    setToast({ show: true, message, type });
    setTimeout(() => {
      setToast({ show: false, message: '', type: '' });
    }, 4000);
  };

  // Load data on component mount and set up auto-refresh
  useEffect(() => {
    loadAllData();
    
    // Auto-refresh every 30 seconds to catch new requests
    const interval = setInterval(() => {
      loadAllData();
    }, 30000);
    
    return () => clearInterval(interval);
  }, []);

  const loadAllData = async () => {
    await Promise.all([
      loadPinRequests(),
      loadPromoters()
    ]);
  };

  // Filter and search logic
  useEffect(() => {
    let filtered = pinRequests || [];

    // Action type filter
    if (statusFilter !== 'all') {
      filtered = filtered.filter(request => request.actionType === statusFilter);
    }

    // Search filter
    if (searchTerm) {
      filtered = filtered.filter(request => {
        const requestId = (request.requestId || '').toLowerCase();
        const promoterEmail = (request.promoterEmail || '').toLowerCase();
        const promoterName = (request.promoterName || '').toLowerCase();
        const searchLower = (searchTerm || '').toLowerCase();
        return requestId.includes(searchLower) || 
               promoterEmail.includes(searchLower) || 
               promoterName.includes(searchLower);
      });
    }

    // Ensure filtered results are also sorted by date (newest first)
    filtered.sort((a, b) => new Date(b.requestDate) - new Date(a.requestDate));

    setFilteredRequests(filtered);
  }, [pinRequests, searchTerm, statusFilter]);

  // Load PIN transactions using unified service
  const loadPinRequests = async () => {
    try {
      setLoading(true);
      
      // Use unified PIN transaction service
      const transactions = await pinTransactionService.getAllPinTransactions({
        actionType: statusFilter !== 'all' ? statusFilter : undefined
      });
      
      console.log(' PIN transactions loaded:', transactions.length, 'entries');
      
      // Transform for backward compatibility with existing UI
      const transformedRequests = transactions.map(transaction => ({
        id: transaction.id,
        requestId: transaction.transaction_id,
        quantity: transaction.pin_change_value,
        status: 'completed',
        promoterId: transaction.user_id,
        promoterEmail: transaction.userEmail,
        promoterName: transaction.userName,
        promoterGeneratedId: `PROM${String(Math.floor(Math.random() * 9999)).padStart(4, '0')}`,
        requestDate: transaction.created_at,
        responseDate: transaction.created_at,
        adminNotes: transaction.note,
        actionType: transaction.action_type,
        customerName: null,
        displayConfig: transaction.displayConfig,
        formattedAmount: transaction.formattedAmount
      }));
      
      setPinRequests(transformedRequests);
      setFilteredRequests(transformedRequests);
      
    } catch (error) {
      console.error('Error loading PIN transactions:', error);
      setPinRequests([]);
      setFilteredRequests([]);
    } finally {
      setLoading(false);
    }
  };

  // Handle request approval/rejection
  const handleRequestAction = async (request, action) => {
    try {
      setSubmitting(true);
      
      // Get the promoter's current pin requests
      const { data: promoterData, error: fetchError } = await supabase
        .from('promoters')
        .select('pin_requests')
        .eq('id', request.promoterId)
        .single();

      if (fetchError) throw fetchError;

      // Parse current requests
      let requests = [];
      try {
        requests = promoterData.pin_requests ? JSON.parse(promoterData.pin_requests) : [];
      } catch (e) {
        requests = [];
      }

      // Find and update the specific request
      const requestIndex = requests.findIndex(r => r.requestId === request.requestId);
      if (requestIndex !== -1) {
        requests[requestIndex] = {
          ...requests[requestIndex],
          status: action,
          responseDate: new Date().toISOString(),
          adminNotes: approvalData.adminNotes || `Request ${action} by admin`,
          adminId: user.id
        };

        // Update the promoter's pin requests
        const { error: updateRequestError } = await supabase
          .from('promoters')
          .update({
            pin_requests: JSON.stringify(requests),
            updated_at: new Date().toISOString()
          })
          .eq('id', request.promoterId);

        if (updateRequestError) throw updateRequestError;

        // If approving, also add pins to promoter's balance
        if (action === 'approved') {
          // Get current promoter pin balance
          const { data: promoterData, error: promoterFetchError } = await supabase
            .from('promoters')
            .select('pins')
            .eq('id', request.promoterId)
            .single();

          if (promoterFetchError) throw promoterFetchError;

          const currentPins = promoterData?.pins || 0;
          const newPinBalance = currentPins + (request.quantity || 0);

          // Update promoter's pin balance
          const { error: pinUpdateError } = await supabase
            .from('promoters')
            .update({
              pins: newPinBalance,
              updated_at: new Date().toISOString()
            })
            .eq('id', request.promoterId);

          if (pinUpdateError) throw pinUpdateError;

          showToast(` Pin request ${action} successfully!\n\nPromoter's pin balance updated: ${currentPins}  ${newPinBalance} pins`, 'success');
        } else {
          showToast(` Pin request ${action} successfully!`, 'success');
        }

        setShowApprovalModal(false);
        setApprovalData({ action: 'approve', adminNotes: '' });
        await loadPinRequests();
        await loadPromoters(); // Refresh promoter data to show updated pin balance
      } else {
        throw new Error('Request not found');
      }
      
    } catch (error) {
      console.error(`Error ${action} request:`, error);
      showToast(` Failed to ${action} request. Please try again.`, 'error');
    } finally {
      setSubmitting(false);
    }
  };

  // Load promoters from profiles table
  const loadPromoters = async () => {
    try {
      const { data, error } = await supabase
        .from('profiles')
        .select('id, name, email, pins, promoter_id, created_at')
        .eq('role', 'promoter')
        .order('created_at', { ascending: false });

      if (!error && data) {
        const transformedPromoters = data.map((promoter) => ({
          id: promoter.id,
          name: promoter.name || 'Unknown',
          email: promoter.email || 'Unknown',
          promoter_id: promoter.promoter_id || 'N/A', // Use real promoter_id from database
          pins: promoter.pins || 0,
          created_at: promoter.created_at
        }));
        
        setPromoters(transformedPromoters);
        setFilteredPromoters(transformedPromoters);
        console.log(' Admin loaded promoters:', transformedPromoters.length);
      } else {
        console.error('Error loading promoters:', error);
        setPromoters([]);
        setFilteredPromoters([]);
      }
    } catch (error) {
      console.error('Error loading promoters:', error);
      setPromoters([]);
      setFilteredPromoters([]);
    }
  };

  // Open approval modal
  const openApprovalModal = (request, action) => {
    setSelectedRequest(request);
    setApprovalData({ action, adminNotes: '' });
    setShowApprovalModal(true);
  };

  // Direct pin allocation functions
  const openPinModal = (promoter = null) => {
    setEditingPromoter(promoter);
    setFormData({
      promoterId: promoter?.id || '',
      pins: '',
      action: 'add'
    });
    setFormErrors({});
    setShowPinModal(true);
  };

  const closePinModal = () => {
    setShowPinModal(false);
    setEditingPromoter(null);
    setFormData({
      promoterId: '',
      pins: '',
      action: 'add'
    });
    setFormErrors({});
  };

  // Form validation for direct pin allocation
  const validateForm = () => {
    const errors = {};
    
    if (!formData.promoterId) {
      errors.promoterId = 'Please select a promoter';
    }
    
    if (!formData.pins || formData.pins <= 0) {
      errors.pins = 'Please enter a valid number of pins';
    }

    setFormErrors(errors);
    return Object.keys(errors).length === 0;
  };

  // Handle direct pin allocation using unified service
  const handlePinAllocation = async (e) => {
    e.preventDefault();
    
    if (!validateForm()) return;

    setSubmitting(true);
    try {
      const promoter = promoters.find(p => p.id === formData.promoterId) || editingPromoter;
      if (!promoter) {
        throw new Error('Promoter not found');
      }

      const pinsToChange = parseInt(formData.pins);
      
      // Use unified PIN transaction service
      let result;
      if (formData.action === 'add') {
        result = await pinTransactionService.adminAllocatePins(
          promoter.id,
          pinsToChange,
          user.id
        );
      } else {
        result = await pinTransactionService.adminDeductPins(
          promoter.id,
          pinsToChange,
          user.id
        );
      }

      if (!result.success) {
        throw new Error(result.error || 'PIN transaction failed');
      }

      showToast(
        ` Successfully ${formData.action === 'add' ? 'allocated' : 'deducted'} ${pinsToChange} PIN${pinsToChange > 1 ? 's' : ''} ${formData.action === 'add' ? 'to' : 'from'} ${promoter.name}!\n\nNew balance: ${result.balance_after} PINs`,
        'success'
      );
      
      closePinModal();
      await loadPromoters();
      await loadPinRequests();
      
    } catch (error) {
      console.error('Error updating pins:', error);
      showToast(` Failed to ${formData.action} pins. Error: ${error.message}`, 'error');
    } finally {
      setSubmitting(false);
    }
  };


  return (
    <>
      <SharedStyles />
      <AdminNavbar />
      <UnifiedBackground>
        <div className="min-h-screen p-8">
          <div className="max-w-7xl mx-auto">
            {/* Header */}
            <div className="flex justify-between items-center mb-8" data-animate>
              <div>
                <h1 className="text-4xl font-bold text-white mb-2">Pin Management System</h1>
                <p className="text-gray-300">Manage pin requests and direct pin allocation</p>
              </div>
              <UnifiedButton
                onClick={() => openPinModal()}
                className="flex items-center space-x-2"
              >
                <Plus className="w-5 h-5" />
                <span>Allocate Pins</span>
              </UnifiedButton>
            </div>

            {/* Tab Navigation */}
            <div className="flex space-x-1 mb-8" data-animate>
              {[
                { id: 'requests', label: 'Pin History', icon: FileText },
                { id: 'pin-requests', label: 'PIN Requests', icon: Send },
                { id: 'direct', label: 'Manage Pins', icon: Users }
              ].map((tab) => {
                const Icon = tab.icon;
                return (
                  <button
                    key={tab.id}
                    onClick={() => setActiveTab(tab.id)}
                    className={`flex items-center space-x-2 px-6 py-3 rounded-lg font-medium transition-all duration-300 ${
                      activeTab === tab.id
                        ? 'bg-gradient-to-r from-indigo-500 to-purple-600 text-white shadow-lg'
                        : 'bg-gray-800/50 text-gray-400 hover:bg-gray-700/50 hover:text-white'
                    }`}
                  >
                    <Icon className="w-5 h-5" />
                    <span>{tab.label}</span>
                  </button>
                );
              })}
            </div>

            {/* Stats Cards - Using Unified Component */}
            <div className="mb-8" data-animate>
              <PinStatsCards 
                stats={{
                  totalTransactions: pinRequests.length,
                  customerCreations: pinRequests.filter(r => r.actionType === PIN_ACTION_TYPES.CUSTOMER_CREATION).length,
                  adminAllocations: pinRequests.filter(r => r.actionType === PIN_ACTION_TYPES.ADMIN_ALLOCATION).length,
                  adminDeductions: pinRequests.filter(r => r.actionType === PIN_ACTION_TYPES.ADMIN_DEDUCTION).length,
                  totalPinsAllocated: pinRequests
                    .filter(r => r.actionType === PIN_ACTION_TYPES.ADMIN_ALLOCATION)
                    .reduce((total, request) => total + Math.abs(request.quantity || 0), 0),
                  totalPinsDeducted: pinRequests
                    .filter(r => r.actionType !== PIN_ACTION_TYPES.ADMIN_ALLOCATION)
                    .reduce((total, request) => total + Math.abs(request.quantity || 0), 0)
                }}
                loading={loading}
              />
            </div>

            {/* Search and Filters */}
            <UnifiedCard className="p-6 mb-8" variant="glassDark" data-animate>
              <div className="flex flex-col md:flex-row gap-4">
                <div className="flex-1 relative">
                  <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-5 h-5" />
                  <input
                    type="text"
                    placeholder="Search by transaction ID (BPV-CC01, BPV-AA01), promoter name, or email..."
                    value={searchTerm}
                    onChange={(e) => setSearchTerm(e.target.value)}
                    className="w-full pl-10 pr-4 py-2 bg-gray-700 border border-gray-600 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-green-500"
                  />
                </div>
                <div className="md:w-48">
                  <select
                    value={statusFilter}
                    onChange={(e) => setStatusFilter(e.target.value)}
                    className="w-full px-3 py-2 bg-gray-700 border border-gray-600 rounded-lg text-white focus:outline-none focus:ring-2 focus:ring-green-500"
                  >
                    <option value="all">All Actions</option>
                    <option value="customer_creation">Customer Creation</option>
                    <option value="admin_allocation">Admin Allocation</option>
                    <option value="admin_deduction">Admin Deduction</option>
                  </select>
                </div>
              </div>
            </UnifiedCard>

            {/* Pin History Tab */}
            {activeTab === 'requests' && (
              <UnifiedCard className="overflow-hidden" variant="glassDark" data-animate>
              {loading ? (
                <SkeletonTable rows={6} columns={5} />
              ) : (
                <div className="overflow-x-auto">
                  <table className="w-full">
                    <thead className="bg-gray-800/50">
                      <tr>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Transaction ID</th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Promoter</th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Pins</th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Action Type</th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Date</th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Notes</th>
                      </tr>
                    </thead>
                    <tbody className="divide-y divide-gray-700/50">
                      {filteredRequests.length === 0 ? (
                        <tr>
                          <td colSpan="6" className="px-6 py-12 text-center text-gray-400">
                            No pin transactions found
                          </td>
                        </tr>
                      ) : (
                        filteredRequests.map((request) => (
                          <tr key={request.id} className="hover:bg-gray-800/30 transition-colors">
                            <td className="px-6 py-4 whitespace-nowrap">
                              <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-purple-100 text-purple-800">
                                {request.requestId}
                              </span>
                            </td>
                            <td className="px-6 py-4 whitespace-nowrap">
                              <div className="text-sm font-medium text-white">{request.promoterName}</div>
                              <div className="text-xs text-gray-400">{request.promoterEmail}</div>
                            </td>
                            <td className="px-6 py-4 whitespace-nowrap">
                              <div className="flex items-center">
                                {(() => {
                                  // Admin allocations should show as positive (+), Customer creation should show as negative (-)
                                  let displayValue, isPositive;
                                  
                                  if (request.actionType === 'admin_allocation') {
                                    displayValue = Math.abs(request.quantity);
                                    isPositive = true;
                                  } else if (request.actionType === 'customer_creation') {
                                    displayValue = Math.abs(request.quantity);
                                    isPositive = false;
                                  } else {
                                    displayValue = request.quantity;
                                    isPositive = request.quantity > 0;
                                  }
                                  
                                  return (
                                    <>
                                      <Pin className={`w-4 h-4 mr-2 ${isPositive ? 'text-green-400' : 'text-red-400'}`} />
                                      <span className={`text-lg font-bold ${isPositive ? 'text-green-300' : 'text-red-300'}`}>
                                        {isPositive ? '+' : '-'}{displayValue}
                                      </span>
                                    </>
                                  );
                                })()}
                              </div>
                            </td>
                            <td className="px-6 py-4 whitespace-nowrap">
                              <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${
                                request.actionType === 'customer_creation' ? 'bg-blue-100 text-blue-800' :
                                request.actionType === 'admin_allocation' ? 'bg-green-100 text-green-800' :
                                request.actionType === 'admin_deduction' ? 'bg-red-100 text-red-800' :
                                'bg-gray-100 text-gray-800'
                              }`}>
                                {request.actionType === 'customer_creation' ? ' Customer Creation' :
                                 request.actionType === 'admin_allocation' ? ' Admin Allocation' :
                                 request.actionType === 'admin_deduction' ? ' Admin Deduction' :
                                 ' Unknown'}
                              </span>
                            </td>
                            <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-300">
                              {request.requestDate ? new Date(request.requestDate).toLocaleDateString() : 'N/A'}
                            </td>
                            <td className="px-6 py-4 text-sm text-gray-300 max-w-xs truncate">
                              {request.adminNotes || 'No notes'}
                            </td>
                          </tr>
                        ))
                      )}
                    </tbody>
                  </table>
                </div>
              )}
            </UnifiedCard>
            )}

            {/* PIN Requests Tab */}
            {activeTab === 'pin-requests' && (
              <AdminPinRequestsTab 
                adminId={user?.id}
                onSuccess={showToast}
                onError={(message) => showToast(message, 'error')}
              />
            )}

            {/* Direct Allocation Tab */}
            {activeTab === 'direct' && (
              <UnifiedCard className="overflow-hidden" variant="glassDark" data-animate>
                {loading ? (
                  <SkeletonTable rows={6} columns={5} />
                ) : (
                  <div className="overflow-x-auto">
                    <table className="w-full">
                      <thead className="bg-gray-800/50">
                        <tr>
                          <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Promoter</th>
                          <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Email</th>
                          <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Current Pins</th>
                          <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Join Date</th>
                          <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Actions</th>
                        </tr>
                      </thead>
                      <tbody className="divide-y divide-gray-700/50">
                        {promoters.length === 0 ? (
                          <tr>
                            <td colSpan="5" className="px-6 py-12 text-center text-gray-400">
                              No promoters found
                            </td>
                          </tr>
                        ) : (
                          promoters.map((promoter) => (
                            <tr key={promoter.id} className="hover:bg-gray-800/30 transition-colors">
                              <td className="px-6 py-4 whitespace-nowrap">
                                <div className="flex items-center">
                                  <div className="w-8 h-8 bg-gradient-to-r from-purple-400 to-pink-500 rounded-full flex items-center justify-center mr-3">
                                    <Users className="w-4 h-4 text-white" />
                                  </div>
                                  <div className="text-sm font-medium text-white">{promoter.name || 'Unknown'}</div>
                                </div>
                              </td>
                              <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-300">
                                {promoter.email}
                              </td>
                              <td className="px-6 py-4 whitespace-nowrap">
                                <div className="flex items-center">
                                  <Pin className="w-4 h-4 text-purple-400 mr-2" />
                                  <span className="text-lg font-bold text-white">{promoter.pins || 0}</span>
                                </div>
                              </td>
                              <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-300">
                                {promoter.created_at ? new Date(promoter.created_at).toLocaleDateString() : 'N/A'}
                              </td>
                              <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                                <div className="flex space-x-2">
                                  <button
                                    onClick={() => openPinModal(promoter)}
                                    className="text-purple-400 hover:text-purple-300 transition-colors"
                                    title="Manage Pins"
                                  >
                                    <Edit className="w-4 h-4" />
                                  </button>
                                </div>
                              </td>
                            </tr>
                          ))
                        )}
                      </tbody>
                    </table>
                  </div>
                )}
              </UnifiedCard>
            )}
          </div>
        </div>

        {/* Approval Modal */}
        {showApprovalModal && selectedRequest && (
          <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
            <UnifiedCard className="w-full max-w-lg mx-4" variant="glassDark">
              <div className="p-6">
                <div className="flex justify-between items-center mb-4">
                  <h3 className="text-lg font-semibold text-white">
                    {approvalData.action === 'approved' ? 'Approve' : 'Reject'} Pin Request
                  </h3>
                  <button
                    onClick={() => setShowApprovalModal(false)}
                    className="text-gray-400 hover:text-white transition-colors"
                  >
                    <X className="w-5 h-5" />
                  </button>
                </div>

                <div className="space-y-6">
                  <div className="bg-gray-700/50 p-4 rounded-lg">
                    <div className="space-y-3 text-sm">
                      <div className="flex justify-between items-center">
                        <span className="text-gray-400">Transaction ID:</span>
                        <span className="text-white font-medium font-mono">{selectedRequest.requestId}</span>
                      </div>
                      <div className="flex justify-between items-center">
                        <span className="text-gray-400">User:</span>
                        <span className="text-white font-medium">{selectedRequest.promoterName}</span>
                      </div>
                      <div className="flex justify-between items-center">
                        <span className="text-gray-400">Action Type:</span>
                        <ActionTypeBadge actionType={selectedRequest.actionType} size="small" />
                      </div>
                      <div className="flex justify-between items-center">
                        <span className="text-gray-400">PIN Change:</span>
                        <PinChangeIndicator 
                          actionType={selectedRequest.actionType} 
                          amount={selectedRequest.quantity}
                          size="small"
                        />
                      </div>
                      <div className="flex justify-between items-center">
                        <span className="text-gray-400">Date:</span>
                        <span className="text-white font-medium">
                          {selectedRequest.requestDate ? new Date(selectedRequest.requestDate).toLocaleDateString() : 'N/A'}
                        </span>
                      </div>
                    </div>
                  </div>

                  <div>
                    <label className="block text-gray-300 text-sm mb-2">
                      Admin Notes
                    </label>
                    <textarea
                      value={approvalData.adminNotes}
                      onChange={(e) => setApprovalData({...approvalData, adminNotes: e.target.value})}
                      placeholder={`Add notes for ${approvalData.action} this request...`}
                      rows={3}
                      className="w-full px-3 py-2 bg-gray-700 border border-gray-600 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-green-500 resize-none"
                    />
                  </div>
                </div>

                <div className="flex justify-end space-x-3 mt-6">
                  <UnifiedButton
                    variant="secondary"
                    onClick={() => setShowApprovalModal(false)}
                  >
                    Cancel
                  </UnifiedButton>
                  <UnifiedButton
                    onClick={() => handleRequestAction(selectedRequest, approvalData.action)}
                    disabled={submitting}
                    className={`flex items-center space-x-2 ${
                      approvalData.action === 'approved' ? 'bg-green-600 hover:bg-green-700' : 'bg-red-600 hover:bg-red-700'
                    }`}
                  >
                    {submitting ? (
                      <>
                        <Loader className="w-4 h-4 bp-animate-spin" />
                        <span>Processing...</span>
                      </>
                    ) : (
                      <>
                        {approvalData.action === 'approved' ? <CheckCircle className="w-4 h-4" /> : <XCircle className="w-4 h-4" />}
                        <span>{approvalData.action === 'approved' ? 'Approve' : 'Reject'} Request</span>
                      </>
                    )}
                  </UnifiedButton>
                </div>
              </div>
            </UnifiedCard>
          </div>
        )}

        {/* Direct Pin Allocation Modal */}
        {showPinModal && (
          <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
            <UnifiedCard className="w-full max-w-md mx-4" variant="glassDark">
              <div className="p-6">
                <div className="flex justify-between items-center mb-4">
                  <h3 className="text-lg font-semibold text-white">
                    {editingPromoter ? `Manage Pins - ${editingPromoter.name}` : 'Allocate Pins'}
                  </h3>
                  <button
                    onClick={closePinModal}
                    className="text-gray-400 hover:text-white transition-colors"
                  >
                    <X className="w-5 h-5" />
                  </button>
                </div>

                <form onSubmit={handlePinAllocation} className="space-y-4">
                  {!editingPromoter && (
                    <div>
                      <label className="block text-sm font-medium text-gray-300 mb-1">
                        Select Promoter *
                      </label>
                      <select
                        value={formData.promoterId}
                        onChange={(e) => setFormData({...formData, promoterId: e.target.value})}
                        className={`w-full px-3 py-2 bg-gray-700 border rounded-lg text-white focus:outline-none focus:ring-2 focus:ring-purple-500 ${
                          formErrors.promoterId ? 'border-red-500' : 'border-gray-600'
                        }`}
                      >
                        <option value="">Select promoter</option>
                        {promoters.map((promoter) => (
                          <option key={promoter.id} value={promoter.id}>
                            {promoter.name} ({promoter.promoter_id}) - {promoter.pins || 0} pins
                          </option>
                        ))}
                      </select>
                      {formErrors.promoterId && (
                        <p className="text-red-400 text-sm mt-1">{formErrors.promoterId}</p>
                      )}
                    </div>
                  )}

                  {editingPromoter && (
                    <div className="bg-gray-700/50 p-4 rounded-lg">
                      <div className="flex items-center justify-between">
                        <div>
                          <p className="text-white font-medium">{editingPromoter.name}</p>
                          <p className="text-gray-400 text-sm">{editingPromoter.email}</p>
                        </div>
                        <div className="text-right">
                          <p className="text-gray-400 text-sm">Current Pins</p>
                          <p className="text-white font-bold text-lg">{editingPromoter.pins || 0}</p>
                        </div>
                      </div>
                    </div>
                  )}

                  <div>
                    <label className="block text-sm font-medium text-gray-300 mb-1">
                      Action *
                    </label>
                    <select
                      value={formData.action}
                      onChange={(e) => setFormData({...formData, action: e.target.value})}
                      className="w-full px-3 py-2 bg-gray-700 border border-gray-600 rounded-lg text-white focus:outline-none focus:ring-2 focus:ring-purple-500"
                    >
                      <option value="add">Add Pins</option>
                      <option value="subtract">Subtract Pins</option>
                    </select>
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-300 mb-1">
                      Number of Pins *
                    </label>
                    <input
                      type="number"
                      min="1"
                      value={formData.pins}
                      onChange={(e) => setFormData({...formData, pins: e.target.value})}
                      className={`w-full px-3 py-2 bg-gray-700 border rounded-lg text-white focus:outline-none focus:ring-2 focus:ring-purple-500 ${
                        formErrors.pins ? 'border-red-500' : 'border-gray-600'
                      }`}
                      placeholder="Enter number of pins"
                    />
                    {formErrors.pins && (
                      <p className="text-red-400 text-sm mt-1">{formErrors.pins}</p>
                    )}
                  </div>

                  <div className="flex justify-end space-x-3 pt-4">
                    <UnifiedButton
                      type="button"
                      variant="secondary"
                      onClick={closePinModal}
                    >
                      Cancel
                    </UnifiedButton>
                    <UnifiedButton
                      type="submit"
                      disabled={submitting}
                      className="flex items-center space-x-2"
                    >
                      {submitting ? (
                        <>
                          <Loader className="w-4 h-4 bp-animate-spin" />
                          <span>Processing...</span>
                        </>
                      ) : (
                        <>
                          <Pin className="w-4 h-4" />
                          <span>{formData.action === 'add' ? 'Add Pins' : 'Subtract Pins'}</span>
                        </>
                      )}
                    </UnifiedButton>
                  </div>
                </form>
              </div>
            </UnifiedCard>
          </div>
        )}

        {/* Toast Notification */}
        {toast.show && (
          <div className={`fixed top-4 right-4 z-50 max-w-md p-4 rounded-lg shadow-lg border transition-all duration-300 ${
            toast.type === 'success' 
              ? 'bg-green-500/10 border-green-500/30 text-green-300' 
              : 'bg-red-500/10 border-red-500/30 text-red-300'
          }`}>
            <div className="flex items-start space-x-3">
              <div className={`w-6 h-6 rounded-full flex items-center justify-center ${
                toast.type === 'success' ? 'bg-green-500' : 'bg-red-500'
              }`}>
                {toast.type === 'success' ? (
                  <svg className="w-4 h-4 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                  </svg>
                ) : (
                  <svg className="w-4 h-4 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                  </svg>
                )}
              </div>
              <div className="flex-1">
                <p className="text-sm font-medium whitespace-pre-line">{toast.message}</p>
              </div>
              <button
                onClick={() => setToast({ show: false, message: '', type: '' })}
                className="text-gray-400 hover:text-white transition-colors"
              >
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
            </div>
          </div>
        )}

      </UnifiedBackground>
    </>
  );
}

export default AdminPins;
